import { Module } from '@nestjs/common';

import {
  ProgressCheckReportScenario,
  ProjectBudgetChangeScenario,
  ProjectCloseReportScenario,
  ProjectCommonReportScenario,
  ProjectMeetingReportScenario,
  ProjectStatusUpdateReportScenario,
  ProjectWorkerReportScenario,
} from '../application/scenario';
import { ReportRepository } from '../domain/repository';
import { ReportTable } from '../infrastructure/persistence/project-report';
import {
  ReportCreateController,
  ReportUpdateController,
} from '../presentation/report';

import { PrismaModule } from './prisma.module';
import { UtilityModule } from './utility.module';

@Module({
  imports: [UtilityModule, PrismaModule],
  controllers: [ReportCreateController, ReportUpdateController],
  providers: [
    ProgressCheckReportScenario,
    ProjectBudgetChangeScenario,
    ProjectCloseReportScenario,
    ProjectCommonReportScenario,
    ProjectMeetingReportScenario,
    ProjectStatusUpdateReportScenario,
    ProjectWorkerReportScenario,
    {
      provide: ReportRepository,
      useClass: ReportTable,
    },
  ],
  exports: [],
})
export class ReportModule {}
